import 'focus-visible';
import React from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { MarkProps } from 'core/types/mark';

import { IconClose } from 'core/src/icons';

import styles from './styles.scss';

const cd = cssDecorator(styles);

/**
 * @description Компонент "Метки", с возможностью скрывать метку при клике на крестик. Крестик
 *  появляется если в компонент передана фун-я для обработки события скрытия. В компоненте присутствует
 *  возможность добавить иконки слева, справа или с обеих сторон от текста метки.
 *  Компонент может быть окрашен в любые цвета, переданные в пропсы в формате "var(—ds-...)" или "#fff"
 */
const Mark: React.FunctionComponent<MarkProps> = ({
  text,
  size,
  iconLeft,
  iconRight,
  onClose,
  dataTestId,
  dataId,
  textColor,
  backgroundColor,
  disabled
}: MarkProps) => {
  const style: React.CSSProperties = {};

  const textClassNames = cd(
    'mark__text',
    {
      [`${ size }`]: true,
      'add-space--left': iconLeft,
      'add-space--right': iconRight
    }
  );

  if (!disabled && backgroundColor) {
    style.backgroundColor = backgroundColor;
  }
  if (!disabled && textColor) {
    style.color = textColor;
  }

  return (
    <div
      className={ cd('mark', { 'reduce-space--right': onClose, disabled }) }
      data-test-id={ dataTestId }
      style={ style }
    >
      { iconLeft }
      <span className={ textClassNames }>
        { text }
      </span>
      { iconRight }
      {
      onClose && (
        <button
          className={ cd('close_button', { active: !disabled, disabled }) }
          type="button"
          onClick={ onClose }
          tabIndex={ 0 }
          disabled={ disabled }
          data-id={ dataId }
        >
          <IconClose color="primary" />
        </button>
      )
    }
    </div>
  );
};

Mark.defaultProps = {
  size: 'default'
};

export { Mark };
